@font-face {
  font-family: "ConcertOne-Regular";
  src: url("../fonts/ConcertOne-Regular.ttf");
}

.todo-input {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input-group__input {
  padding: 1rem 8rem;
  font-size: 1.2rem;
  font-family: "ConcertOne-Regular", Courier, monospace;
  border: none;
  outline: none;
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  margin-right: 1rem;
}

.input-group__input::placeholder {
  font-size: 1.2rem;
}

.todo-status {
  padding: 1rem;
  border: none;
  outline: none;
  border-radius: 1rem;
  font-family: "ConcertOne-Regular", Courier, monospace;
  font-size: 1.2rem;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.todo-input-box {
  position: relative;
}

.todo-input-box svg {
  position: absolute;
  width: 2rem;
  height: 2rem;
  top: 0.8rem;
  right: 1rem;
  cursor: pointer;
}

.todos__wrapper {
  /* width: 1400px; */
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  /* background-color: red; */
}

.shape-3 {
  position: absolute;
  right: 20rem;
  top: 20rem;
  z-index: -100;
}

@media screen and (max-width: 640px) {
  .shape-3 {
    right: 2rem;
  }
  .todo-input {
    flex-wrap: wrap;
  }
  .todo-status {
    margin-top: 2rem;
  }
}
@media screen and (max-width: 470px) {
  .input-group__input {
    padding: 1rem 5rem;
  }
  .todo-shape1 {
    left: -10rem;
  }
  .todo-shape2 {
    right: -5rem;
  }
}
@media screen and (max-width: 380px) {
  .input-group__input {
    padding: 1rem 2rem;
  }
}
